export default {
	props:{
		isCenter : {
			type : Boolean,
			default : false
		},
		currentIndex : {
			type : Number,
			default : 0
		},
		size : {
			type : Number,
			default : 120
		},
		items : {
			type : Array,
			default : function () {
				return []
			}
		},
		activeLineBg : {
			type : String,
			default : "linear-gradient(to right, #66BFFF,#3388FF)"
		},
		activeColor:{
			type : String,
			default : "#333333"
		},
		activeLineHeight : {
			type : String,
			default : '6rpx'
		},
		activeLineWidth : {
			type : String,
			default : "36rpx"
		},
		color : {
			type : String,
			default : "#333333"
		},
		margin : {
			type : Number,
			default : 0
		},
		textAlign : {
			type : String,
			default : 'left'
		},
		lineHeight : {
			type : String,
			default : '50rpx'
		},
		fontSize : {
			type : String,
			default : '28rpx'
		}
	},
	methods:{
		navchang : function (e){
			this.$emit('change', Number(e.currentTarget.dataset.index))
		}
	}
}
